import { Component, OnInit, NgZone } from "@angular/core";
import { ToastrService } from 'ngx-toastr';
import { TwitchService } from 'src/app/services/twitch.service';
import { Router } from '@angular/router';
import { SharedService } from 'src/app/services/shared.service';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { NotificationType } from 'src/app/enums/notification-type';

@Component({
  selector: "app-notifications",
  templateUrl: "notifications.component.html"
})
export class NotificationsComponent implements OnInit {
  notifications: any[];
  activeNotification: any;
  userToMessage: any;

  constructor(private toastr: ToastrService,
    private twitchService: TwitchService,
    private router: Router,
    private zone: NgZone,
    private sharedService: SharedService,
    private modalService: NgbModal) {}

  public get notificationType(): typeof NotificationType {
    return NotificationType;
  }

  ngOnInit() {
    this.sharedService.sendNotificationEvent();
    this.getNotifications();

    this.sharedService.sendNavigationEvent(false);
  }

  getNotifications() {
    this.twitchService.getNotifications().subscribe((data: any) => {
      this.notifications = data.notifications;
    }, err => {
      this.toastr.error('Unable to retrieve user notifications.', 'Error');
    });
  }

  deleteNotification(id) {
    this.twitchService.deleteNotification(id).subscribe((data: any) => {}, err => {});
  }

  handleNotification(notification, messageUserContent) {
    if (notification.notificationTypeId == NotificationType.Follow) {
      this.searchNotification(notification);
      notification.enabled = false;
    } else if (notification.notificationTypeId == NotificationType.Message) {
      this.activeNotification = notification;
      this.openMessageModal(messageUserContent, notification.follower);
    } else if (notification.notificationTypeId == NotificationType.Mention) {
      this.router.navigate(['./forum']);
      notification.enabled = false;
      this.deleteNotification(notification.id);
    }
  }

  searchNotification(notification) {
    sessionStorage.setItem('query', notification.follower.username);
    this.deleteNotification(notification.id);
    this.zone.run(() => this.router.navigate(['/dashboard']));
  }

  openMessageModal(content, user) {
    this.modalService.open(content, {
      windowClass: 'modal-search'
    }).result.then((result) => {}, (reason) => {});
    this.userToMessage = user;
  }

  messageUser(userMessage) {
    this.activeNotification.enabled = false;
    this.deleteNotification(this.activeNotification.id);

    let messageNotification = {
      fromUserId: localStorage.getItem('userId'),
      toUserId: this.userToMessage.userId,
      message: userMessage
    };

    this.twitchService.messageUser(messageNotification).subscribe(() => {
      this.toastr.info(`Message sent to ${this.userToMessage.username}.`);
    }, err => {
      this.toastr.error('Unable to message user.', 'Error');
    });
  }
}
